$orange-text: orangered; 
$white-text: white; 
$black-text: rgb(33, 41, 49); 

$title-font: 'Source Sans Pro', sans-serif; 
$type-writer-font: 'Courier New', Courier, monospace; 
$sub-text-font: 'Merriweather', serif; 

$title-size: 1.5em; 
$title-sub-size: 1.25em; 
$sub-text-size: 0.95em; 
$sub-sub-text-size: 0.85em; 


.center {
    display: flex; 
    justify-content: center; 
    align-items: center; 
} 

body {
    margin: 0px; 

    .back-img-container {
        position: fixed; 
        width: 100%; 
        height: 100%; 
        z-index: -10; 

        .back-img {
            width: 100%; 
            height: 100vh; 
            background: linear-gradient(180deg, rgb(100, 55, 39) 0%, rgb(41, 19, 9) 100%); 
            background-position: bottom; 
            background-repeat: no-repeat; 
            background-size: cover;  
        }
    } 

    .about-us-content-top-page {
        width: 100%; 
        height: 25vh; 
        @extend .center; 
        padding: 50px 0px; 

        button { 
            position: relative; 
            width: 250px; 
            height: 55px; 
            background-color: rgba(0, 0, 0, 0); 
            color: $orange-text; 
            border: 2px solid $orange-text; 
            font-weight: 600; 
            cursor: pointer; 
            transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1); 
            outline: none; 
            z-index: 5; 
            opacity: 1; 

            &:hover {
                color: $white-text !important; 
                border: none; 
            } 

            &:hover::before {
                left: 0%; 
                right: auto; 
                width: 100%; 
            } 

            &::before {
                position: absolute; 
                top: 0px; 
                right: 0px; 
                height: 100%; 
                width: 0px; 
                z-index: -1; 
                content: '';  
                background: $orange-text; 
                transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1); 
            }
        }
    } 

    .about-us-content-page {
        width: 100%; 
        height: auto; 
        background-color: $white-text; 
        @extend .center; 
        flex-direction: column; 

        .top-section-container {
            width: 95%; 
            height: auto; 
            @extend .center; 
            flex-direction: column; 
            font-family: $title-font; 
            

            h1 {
                text-align: center; 
                font-family: $title-font; 
                font-size: $title-size + 0.25em; 
                padding-top: 45px; 
            } 

            img {
                width: 85%; 
                height: auto; 
            } 

            p {
                tab-size: 4; 
                width: 85%; 
                font-family: $sub-text-font; 
                font-size: $sub-text-size; 
                font-weight: 100; 
                line-height: 2rem; 
                margin: 10px 0px; 
            } 

            .about-us-container {
                width: 100%; 
                height: auto; 
                @extend .center; 
                flex-direction: column; 

                .about-us-section {
                    width: 100%; 
                    height: auto; 
                    @extend .center; 
                    flex-direction: column; 
                    margin: 20px 0px; 

                    ul, ol {
                        width: 80%; 
                        font-family: $sub-text-font; 
                        font-size: $sub-text-size; 
                        line-height: 2rem; 
                        margin: 10px 0px; 

                        li {
                            margin: 15px 0px; 
                        }
                    } 

                    #content {
                        width: 100%; 
                        @extend .center; 
                        flex-direction: column; 
                        margin: 25px 0px; 

                        img {
                            width: 70%; 
                            height: auto; 
                            margin: 20px 0px; 
                        } 

                        h2 {
                            width: 85%; 
                            text-align: left; 
                            font-family: $title-font; 
                            font-size: $title-size; 
                            padding: 0px; 
                            margin: 25px 0px; 
                        }

                        h3 {
                            width: 85%; 
                            text-align: left; 
                            font-family: $title-font; 
                            font-size: $title-size - 0.1em; 
                            padding: 0px; 
                        } 

                        p {
                            tab-size: 4; 
                            width: 85%; 
                            font-family: $sub-text-font; 
                            font-size: $sub-text-size; 
                            font-weight: 100; 
                            line-height: 2rem; 
                            margin: 10px 0px; 
                        } 

                        span {
                            width: 75%; 
                            font-family: $sub-text-font; 
                            font-size: $sub-sub-text-size; 
                            font-weight: 100; 
                            line-height: 2rem; 
                            margin: 10px 0px;  
                            text-align: center; 
                        }
                    }
                }
            }
        }
    }
} 

@media only screen and (min-width: 1700px) {
    body .about-us-content-page .top-section-container .about-us-container .about-us-section {
        width: 85%; 
    }
} 

@media only screen and (min-width: 2000px) {
    body .about-us-content-page .top-section-container .about-us-container .about-us-section {
        width: 75%; 
    }
}
